import React from "react";
import { Row, Col } from "react-bootstrap";
const Icon = ({ iconName, subtitle, color, wd = "70" }) => {
    return (
        <Col className="test-col" xs={6} lg={6}>
            <span
                class="iconify"
                data-icon={iconName}
                data-inline="false"
                data-width={wd}
                data-height={wd}
                style={{ color:  color}}
            ></span>
            <p>{subtitle}</p>
        </Col>
    );
};

export default Icon;
